@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;500;700&display=swap');

:root {
  --bg: #05070d;
  --text: #e6e6eb;
  --neon: #7df9ff;
  --glass: rgba(255, 255, 255, 0.07);
  --border: rgba(255, 255, 255, 0.15);
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Space Grotesk', sans-serif;
  color: var(--text);
  background: black;
  overflow-x: hidden;
}

/* Background Video */
.bgVideo {
  position: fixed;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -2;
}

/* Dark overlay */
.overlay {
  position: fixed;
  inset: 0;
  background: rgba(5, 7, 13, 0.65);
  z-index: -1;
}

/* UI Layer */
.ui {
  position: relative;
}

/* Glass effect */
.glass {
  background: var(--glass);
  backdrop-filter: blur(14px);
  border: 1px solid var(--border);
  border-radius: 18px;
}

/* Nav */
.nav {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  padding: 14px 28px;
  display: flex;
  gap: 40px;
  align-items: center;
  z-index: 100;
}

.logo {
  font-weight: 700;
  letter-spacing: 2px;
}

.links a {
  color: var(--text);
  text-decoration: none;
  margin: 0 10px;
  opacity: 0.8;
}

.links a:hover {
  color: var(--neon);
  opacity: 1;
}

/* Hero */
.hero {
  min-height: 100vh;
  display: grid;
  place-items: center;
  text-align: center;
  padding: 0 20px;
}

.hero h1 {
  font-size: clamp(2.5rem, 5vw, 4rem);
  margin-bottom: 10px;
}

.neon {
  color: var(--neon);
  text-shadow: 0 0 12px var(--neon), 0 0 40px var(--neon);
}

.subtitle {
  opacity: 0.75;
}

/* Sections */
.section {
  max-width: 900px;
  margin: 120px auto;
  padding: 40px;
}

.section h2 {
  margin-top: 0;
  margin-bottom: 16px;
}

/* Projects */
.projects {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 24px;
}

.card {
  padding: 24px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-10px);
  box-shadow: 0 0 40px rgba(125, 249, 255, 0.25);
}

/* Footer */
.footer {
  text-align: center;
  padding: 40px;
  opacity: 0.6;
  font-size: 0.9rem;
}

/* Fade-in animation */
.fade {
  opacity: 0;
  transform: translateY(30px);
  transition: 0.8s ease;
}

.fade.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Section */
.projectsSection {
  padding: 140px 6vw;
  text-align: center;
}

.sectionTitle {
  font-size: clamp(2rem, 4vw, 3rem);
  margin-bottom: 80px;
  color: #e9d5ff;
}

/* Grid */
.projectsGrid {
  display: flex;
  gap: 50px;
}


/* Card */
.projectCard {
  position: relative;
  overflow: hidden;
  border-radius: 22px;
  transform-style: preserve-3d;
  transition: transform 0.4s ease, box-shadow 0.4s ease;
  color: #f5e9ff;
  height: 330px;
  width: 450px;
}

.projectCard img {
  width: 100%;
  height: 180px;
  object-fit: cover;
  display: block;
  filter: brightness(0.85);
}

/* Content */
.projectContent {
  padding: 24px;
  text-align: left;
}

.projectContent h3 {
  margin: 0 0 8px;
  color: #f5e9ff;
}

.projectContent p {
  opacity: 0.75;
  font-size: 0.95rem;
  color: #f5e9ff;
  white-space: normal;
}

/* Meta */
.meta {
  margin-top: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.stack span {
  font-size: 0.7rem;
  padding: 4px 8px;
  border-radius: 6px;
  background: rgba(255,255,255,0.08);
  margin-left: 6px;
  color: #f5e9ff
}

/* Hover magic */
.projectCard::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: inherit;
  background: linear-gradient(
    120deg,
    transparent,
    rgba(157, 78, 221, 0.4),
    transparent
  );
  opacity: 0;
  transition: opacity 0.4s;
}

.projectCard:hover::before {
  opacity: 1;
}

.projectCard:hover {
  transform: translateY(-14px) rotateX(6deg);
  box-shadow:
    0 30px 80px rgba(157, 78, 221, 0.35),
    0 0 60px rgba(157, 78, 221, 0.2);
}

.neonBtn {
  margin-top: 80px;
  padding: 14px 36px;
  border-radius: 999px;
  border: none;
  background: linear-gradient(135deg, #9d4edd, #5a4dff);
  color: white;
  font-size: 1rem;
  cursor: pointer;
  box-shadow: 0 0 25px rgba(157, 78, 221, 0.6);
  transition: transform 0.2s, box-shadow 0.2s;
}

.neonBtn:hover {
  transform: scale(1.05);
  box-shadow: 0 0 40px rgba(157, 78, 221, 0.9);
}

/* Modal base */
.modal {
  position: fixed;
  inset: 0;
  background: rgba(5, 7, 13, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 24px;
  z-index: 9999;
  overflow-y: auto;
  transition: opacity 0.3s ease;
}

/* Hidden state */
.modal.hidden {
  opacity: 0;
  pointer-events: none;
}

/* Modal content */
.modalContent {
  max-width: 600px;
  width: 100%;
  padding: 24px 32px;
  border-radius: 20px;
  position: relative;
  color: var(--text);
  box-shadow:
    0 0 40px rgba(157, 78, 221, 0.5),
    0 0 60px rgba(125, 249, 255, 0.4);
}

/* Close button */
#modal-close {
  position: absolute;
  top: 14px;
  right: 18px;
  background: none;
  border: none;
  font-size: 2.5rem;
  color: var(--neon);
  cursor: pointer;
  transition: color 0.3s ease;
}

#modal-close:hover {
  color: #fff;
}

/* Modal Sections */
.modalSection {
  margin-top: 24px;
}

.modalSection h4 {
  margin-bottom: 8px;
  color: var(--neon);
}

.modalSection ul {
  list-style: none;
  padding-left: 0;
}

.modalSection ul li {
  margin-bottom: 6px;
}

/* Link styles inside modal */
.modalSection ul li a {
  color: var(--neon);
  text-decoration: none;
}

.modalSection ul li a:hover {
  text-decoration: underline;
}

/* Scroll lock (apply to body when modal open) */
body.modalOpen {
  overflow: hidden;
}

.moreProjectsSoon {
  display: inline-block;
  margin: 40px auto 0 auto;
  padding: 10px 24px;
  border-radius: 30px;
  background: linear-gradient(135deg, #9d4edd, #5a4dff);
  color: white;
  font-size: 0.9rem;
  font-weight: 600;
  box-shadow: 0 0 15px rgba(157, 78, 221, 0.7);
  animation: pulseGlow 2.5s infinite alternate;
  user-select: none;
  cursor: default;
  max-width: fit-content;
  text-align: center;
}

/* Pulse animation stays same */
@keyframes pulseGlow {
  0% {
    box-shadow: 0 0 12px rgba(157, 78, 221, 0.5);
    transform: scale(1);
  }
  100% {
    box-shadow: 0 0 25px rgba(157, 78, 221, 0.9);
    transform: scale(1.05);
  }
}
