/* ==========================
   0. IMPORTY & ZMIENNE
========================== */

/* Fonty */
@font-face {
  font-family: 'Roboto';
  src: url('../assets/fonts/RobotoDefault.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;500;700&display=swap');

/* Kolory i zmienne CSS */
:root {
  --primary-color: rgb(210, 161, 210);
  --secondary-color: #000103;
  --bg: #05070d;
  --text: #e6e6eb;
  --neon: #7df9ff;
  --glass: rgba(255, 255, 255, 0.07);
  --border: rgba(255, 255, 255, 0.15);
}

/* ==========================
   1. RESET CSS
========================== */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Space Grotesk', sans-serif;
  line-height: 1.5;
  background-color: var(--secondary-color);
  color: #000;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  display: block;
}

/* ==========================
   2. GLOBAL COMPONENTS
========================== */

/* Glass effect */
.glass {
  background: var(--glass);
  backdrop-filter: blur(14px);
  border: 1px solid var(--border);
  border-radius: 18px;
}

/* Fancy button */
.fancy-button {
  cursor: pointer;
  background: linear-gradient(to bottom, #6366f1, #4f46e5);
  box-shadow: 0px 4px 32px rgba(99, 102, 241, 0.7);
  padding: 0.75rem 1.5rem;
  border-radius: 1rem;
  border: 1px solid #94a3b8;
  color: white;
  font-weight: 500;
  position: relative;
  font-family: 'Roboto', sans-serif;
  height: 50px;
  width: 150px;
  margin-top: 75px;
  font-size: 15px;
  transition: transform 0.3s ease;
  overflow: hidden;
}

.fancy-button::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.3);
  transform: skewX(-20deg);
  transition: left 0.5s;
}

.fancy-button:hover::before {
  left: 100%;
}

.fancy-button:hover {
  transform: scale(1.2) !important;
}

/* HR line */
.hrLine {
    background-color: #000000;
    border: none;
    height: 2px;
}

/* ==========================
   3. HEADER / NAV
========================== */

.heading {
    background-color: rgba(0, 0, 0, 0.3);
    color: white;
    padding: 20px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 1000;
}

.heading button::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -15px;
    width: 100%;
    height: 2px;
    background-color: var(--primary-color);
}

.nav-buttons button {
    font-weight: 500;
    font-size: 15px;
    margin-left: 100px;
    cursor: pointer;
    background-color: transparent;
    border: 0px;
    color: var(--primary-color);
    position: relative;
}

.heading_h1 {
    font-weight: 600;
    font-size: 35px;
    color: var(--primary-color);
}

/* Fixed nav */
.nav {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  padding: 14px 28px;
  display: flex;
  gap: 40px;
  align-items: center;
  z-index: 100;
}

.logo {
  font-weight: 700;
  letter-spacing: 2px;
  color: var(--text)
}

.links a {
  color: var(--text);
  text-decoration: none;
  margin: 0 10px;
  opacity: 0.8;
}

.links a:hover {
  color: var(--primary-color);
  opacity: 1;
}

/* ==========================
   4. HERO SECTION
========================== */

.heroDiv {
    position: relative;
    z-index: 1;
    color: white;
    text-align: center;
    top: 50%;
    transform: translateY(-50%);
}

.heroDiv button {
    cursor: pointer;
    background: linear-gradient(to bottom, #6366f1, #4f46e5);
    box-shadow: 0px 4px 32px rgba(99, 102, 241, 0.7);
    padding: 0.75rem 1.5rem;
    border-radius: 1rem;
    border: 1px solid #94a3b8;
    color: white;
    font-weight: 500;
    font-family: inherit;
    height: 50px;
    width: 150px;
    margin-top: 50px;
    font-size: 15px;
    overflow: hidden;
}

.heroDiv button:hover {
    transform: scale(1.1) !important;
}

.heroDiv button::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.3);
  transform: skewX(-20deg);
  transition: left 0.5s;
}

.heroDiv button:hover::before {
  left: 100%;
}

.heroDivButtonAddIfVisible {
   transition: transform 0.3s ease !important;
}

.heroDiv h1 {
    font-size: 60px;
    font-weight: 500;
}

.heroDiv p {
    font-size: 20px;
    margin-top: 20px;
}

/* ==========================
   5. PROJECTS SECTION
========================== */

.project-card {
  background: rgba(255, 255, 255, 0.15) !important;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(10px) !important;
  -webkit-backdrop-filter: blur(10px) !important;
  border-radius: 18px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  width: 500px;
  overflow: hidden;
  border: none;
  outline: none;
  will-change: transform;
  transition: transform 0.3s ease, background 0.3s ease !important;
  height: 425px;
}

.project-card h2 {
  color: var(--primary-color);
  font-weight: 500;
}

.project-card:hover {
  transform: scale(1.03) !important;
}

.header-img {
  border-top-left-radius: 18px;
  border-top-right-radius: 18px;
}

.project-card p.description {
  font-size: 14px;
  color: #555;
  margin-bottom: 10px;
}

.tech-icons, .language-icons {
  display: flex;
  gap: 5px;
  justify-content: center;
}

.tech-icons img, .language-icons img {
  width: 24px;
  height: 24px;
  object-fit: contain;
  margin-bottom: 10px;
}

.description {
    display: flex;
    flex-direction: column;
    align-content: center;
    flex-wrap: wrap;
    align-items: center;
}

.description_Cards {
  display: flex;
  gap: 60px;
}

.description_p {
  text-wrap: wrap;
  font-size: 16px;
  padding-bottom: 20px;
  color: white;
}

.description h1 {
  color: var(--primary-color);
  padding-bottom: 50px;
  font-size: 50px;
  font-weight: 500;
  padding-top: 200px;
}

/* ==========================
   6. ABOUT ME SECTION
========================== */

.AboutMe {
  color: white;
  width: 60%;
  display: flex;
  flex-direction: column;
  align-content: center;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 15vh;
}

.AboutMe h1 {
  color: var(--primary-color);
  padding-bottom: 50px;
  font-size: 50px;
  font-weight: 500;
  transition: all 0.3s ease;
}

.AboutMe p {
  text-wrap: wrap;
  padding-bottom: 50px;
  font-size: 20px;
}

.AboutMeCardsDiv {
  display: flex;
  gap: 30px;
}

.AboutMeCard {
  background: rgba(255, 255, 255, 0.07) !important;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(10px) !important;
  -webkit-backdrop-filter: blur(10px) !important;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 30px;
  color: white;
  width: 250px;
  height: 175px;
  transition: transform 0.3s ease, background 0.3s ease !important;
}

.AboutMeCard:hover {
  background: rgba(255, 255, 255, 0.2) !important;
  transform: scale(1.05) !important;
  transition: transform 0.3s ease !important;
}

.AboutMeCard h2 {
  color: var(--primary-color);
  font-weight: 500;
  margin-bottom: 10px;
  font-size: 35px;
}

/* ==========================
   7. SKILLS / TECHNOLOGIES SECTION
========================== */

.MySkills {
  color: white;
  width: 60%;
  display: flex;
  flex-direction: column;
  align-content: center;
  flex-wrap: wrap;
  align-items: center;
  padding-bottom: 100px;
  padding-top: 150px;
}

.MySkills h1 {
  color: var(--primary-color);
  padding-bottom: 50px;
  font-size: 50px;
  font-weight: 500;
}

.MySkills p {
  text-wrap: wrap;
  padding-bottom: 50px;
  font-size: 20px;
}

.skill-card {
  background: rgba(255, 255, 255, 0.07) !important;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(10px) !important;
  -webkit-backdrop-filter: blur(10px) !important;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 30px;
  color: white;
  width: 200px;
  height: 200px;
  transition: transform 0.3s ease, background 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.skill-card:hover {
  transform: scale(1.05) !important;
  transition: transform 0.3s ease !important;
}

.skill-card img {
  width: 64px;
  height: 64px;
  object-fit: contain;
  margin-bottom: 10px;
  filter: invert(1);
}

.MySkillsLayout {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 15px;
  width: 100%;
  justify-items: center;
  align-items: start;
  padding: 10px;
}

/* ==========================
   8. ANIMATIONS
========================== */

/* About Me fade animations */
@keyframes fadeInUp { from {opacity:0; transform:translateY(30px);} to {opacity:1; transform:translateY(0);} }
@keyframes fadeInDown { from {opacity:0; transform:translateY(-30px);} to {opacity:1; transform:translateY(0);} }

.aboutme-fade { opacity: 0; transform: translateY(30px); transition: opacity 1s, transform 1s; }
.aboutme-fade.animate { opacity: 1; transform: translateY(0); }
.aboutme-fade.delay-1.animate { transition-delay: 0.2s; }
.aboutme-fade.delay-2.animate { transition-delay: 0.6s; }
.aboutme-fade.delay-3.animate { transition-delay: 0.8s; }
.aboutme-fade.delay-4.animate { transition-delay: 1s; }
.aboutme-fade.delay-5.animate { transition-delay: 1.2s; }
.aboutme-fade.delay-6.animate { transition-delay: 1.4s; }
.aboutme-fade.delay-7.animate { transition-delay: 1.6s; }

.aboutme-fade-reversed { opacity: 0; transform: translateY(-30px); transition: opacity 1s, transform 1s; }
.aboutme-fade-reversed.animate { opacity: 1; transform: translateY(0); }

@keyframes heartBeat {
    0%, 100% { transform: scale(1); }
    25% { transform: scale(1.3); }
    50% { transform: scale(1); }
}

/* ==========================
   9. ADDITIONAL STUFF
========================== */
/* ADDITIONAL STUFF - to be sorted */

.hoverClass {
  transition: all 0.3s ease-in-out !important;
  font-weight: 700 !important;
}
.hoverClass:hover {
  text-shadow: 0 0 20px rgba(210, 161, 210, 0.5);
}