/* Responsive */
@media (max-width: 768px) {
    .footer-futuristic {
        padding: 40px 0 30px 0;
        margin-top: 60px;
    }
    
    .footer-content {
        gap: 30px;
    }
    
    .footer-logo {
        font-size: 1.8rem;
    }
    
    .footer-brand,
    .footer-links,
    .footer-social {
        flex: 1 1 100%;
        min-width: 100%;
    }
    
    .social-links {
        justify-content: center;
    }
    
    .footer-divider {
        margin: 30px auto 15px;
    }
}

@media (max-width: 480px) {
    .footer-futuristic {
        padding: 30px 0 20px 0;
        margin-top: 40px;
    }
    
    .footer-content {
        padding: 0 16px;
        gap: 20px;
    }
    
    .footer-logo {
        font-size: 1.5rem;
        letter-spacing: 1px;
    }
    
    .footer-links h4,
    .footer-social h4 {
        font-size: 0.85rem;
    }
    
    .social-link {
        width: 40px;
        height: 40px;
    }
    
    .social-icon {
        width: 20px;
        height: 20px;
    }
}

/* MEDIA QUERIES */

/* Mobile: do 767px */
@media (max-width: 767px) {
  .heading {
      display: flex;
      flex-direction: column;
      align-content: center;
      justify-content: center;
      align-items: center;
      width: 100vw;
  }

  .nav-buttons {
    display: flex;
    width: 100vw;
    justify-content: center;
    gap: 30px
  }

  .nav-buttons button {
    margin: 0;
  }

  .AboutMe {
    width: 100vw;
  }

  .AboutMe p {
    font-size: 1.3em;
    text-wrap: wrap;
    width: 100vw;
  }
    
  .AboutMeCardsDiv {
      width: 100vw;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      align-content: center;
  }

  .AboutMeCard {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
  }

  .description_Cards {
    display: flex;
    flex-direction: column;
    width: 100vw;
    align-content: center;
    justify-content: center;
    align-items: center;
  }

  .description h1 {
    width: 100vw;
  }

  .project-card {
      display: flex;
      flex-direction: column;
      width: 90vw;
  }
}

