/* ==========================
   9. FOOTER
========================== */

.footer {
    color: white;
    padding: 20px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    bottom: 0;
    width: 100%;
    margin-top: 50px;
    font-size: 15px;
}

.footer a {
    color: var(--primary-color);
    text-decoration: none;
}

.footer a:hover {
    text-decoration: underline;
}

.footer-futuristic {
    position: relative;
    width: 100%;
    padding: 60px 0 40px 0;
    margin-top: 100px;
    color: #fff;
    overflow: hidden;
    border-top: 1px solid rgba(210, 161, 210, 0.2);
}

@keyframes footerGlowPulse {
    0%, 100% { opacity: 0.3; }
    50% { opacity: 0.5; }
}

.footer-stars {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.1;
    background-image: 
        radial-gradient(2px 2px at 20px 30px, rgba(210, 161, 210, 0.8), rgba(0,0,0,0)),
        radial-gradient(2px 2px at 60px 70px, rgba(210, 161, 210, 0.5), rgba(0,0,0,0)),
        radial-gradient(1px 1px at 50px 50px, rgba(210, 161, 210, 0.8), rgba(0,0,0,0)),
        radial-gradient(1px 1px at 130px 80px, rgba(210, 161, 210, 0.6), rgba(0,0,0,0)),
        radial-gradient(2px 2px at 90px 10px, rgba(210, 161, 210, 0.5), rgba(0,0,0,0));
    background-size: 200px 200px;
    z-index: 1;
    pointer-events: none;
}

.footer-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 32px;
    gap: 50px;
    position: relative;
    z-index: 2;
}

.footer-logo {
    font-size: 2.2rem;
    font-weight: 700;
    margin-bottom: 16px;
    color: var(--primary-color);
    letter-spacing: 2px;
    transition: all 0.3s ease-in-out;
}

.footer-logo:hover {
    text-shadow: 0 0 20px rgba(210, 161, 210, 0.5);
    transform: scale(1.05);
}

.footer-brand {
    flex: 1 1 220px;
    min-width: 200px;
}

.footer-location,
.footer-email {
    display: flex;
    align-items: center;
    gap: 12px;
    font-size: 0.95rem;
    margin: 12px 0;
    color: rgba(255, 255, 255, 0.8);
    transition: all 0.3s ease;
}

.footer-location:hover,
.footer-email:hover {
    color: var(--primary-color);
}

.footer-icon {
    width: 20px;
    height: 20px;
    flex-shrink: 0;
    transition: all 0.3s ease;
    opacity: 0.8;
}

.footer-location:hover .footer-icon,
.footer-email:hover .footer-icon {
    opacity: 1;
    transform: scale(1.15);
}

.footer-email a {
    color: inherit;
    text-decoration: none;
    font-weight: 500;
    position: relative;
}

.footer-email a:hover {
    color: var(--primary-color);
}

.footer-email a::after {
    content: '';
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 0;
    height: 1px;
    background-color: var(--primary-color);
    transition: width 0.3s ease;
}

.footer-email a:hover::after {
    width: 100%;
}

.footer-links,
.footer-social {
    flex: 1 1 150px;
    min-width: 150px;
}

.footer-links h4,
.footer-social h4 {
    font-size: 0.95rem;
    font-weight: 600;
    margin-bottom: 16px;
    color: var(--primary-color);
    text-transform: uppercase;
    letter-spacing: 1px;
}

.footer-links ul {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.footer-links a {
    color: rgba(255, 255, 255, 0.7);
    text-decoration: none;
    font-size: 0.9rem;
    transition: all 0.3s ease;
    display: inline-block;
}

.footer-links a::before {
    content: '→';
    opacity: 0;
    margin-right: 8px;
    transition: all 0.3s ease;
}

.footer-links a:hover {
    color: var(--primary-color);
    transform: translateX(8px);
}

.footer-links a:hover::before {
    opacity: 1;
}

.social-links {
    display: flex;
    gap: 16px;
    align-items: center;
}

.social-link {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 44px;
    border-radius: 50%;
    background-color: rgba(210, 161, 210, 0.1);
    border: 2px solid rgba(210, 161, 210, 0.3);
    transition: all 0.3s ease;
    color: var(--primary-color);
}

.social-link:hover {
    background-color: rgba(210, 161, 210, 0.2);
    border-color: var(--primary-color);
    box-shadow: 0 0 20px rgba(210, 161, 210, 0.4);
    transform: translateY(-4px);
}

.social-icon {
    width: 22px;
    height: 22px;
}

.footer-divider {
    max-width: 1200px;
    margin: 40px auto 20px;
    height: 1px;
    background: linear-gradient(90deg, transparent, rgba(210, 161, 210, 0.3), transparent);
    position: relative;
    z-index: 2;
}

.footer-bottom {
    text-align: center;
    padding: 0 32px;
    position: relative;
    z-index: 2;
}

.footer-bottom p {
    font-size: 0.9rem;
    color: rgba(255, 255, 255, 0.6);
    margin: 8px 0;
}

.footer-credits {
    font-size: 0.85rem;
    color: rgba(255, 255, 255, 0.4);
    margin-top: 12px;
    transition: all 0.3s ease;
}

.footer-credits:hover {
    color: rgba(255, 255, 255, 0.7);
}

.heart {
    display: inline-block;
    animation: heartBeat 0.6s ease-in-out infinite;
    margin: 0 4px;
}